<template>
  <div v-if="dataRetrieved">
    <div class="row ">
      <div class="col-md-10"></div>
      <div class="col-md-2 ">
        <div class="badge-container">
          <h3>
          <span 
        :class="['badge', {
          'bg-success': classificationStatus === 'Classififcation Approved',
          'bg-warning text-dark': classificationStatus === 'Classification In Progress',
          'bg-danger': classificationStatus === 'Not Classified'
        }, 'fixed-badge']">
        {{ this.classificationStatus }}
      </span>
        </h3>
        </div>
      </div>
    </div>
    <h1>Entry Details</h1>
  <h2>Reference Number: {{ this.entryDetails?.entry_number }} </h2>
  
  <CustomsEntryForm ref="myCustomsForm" :customsEntryForm = "customsEntryForm" @update-customs-entry-form="customsEntryForm = $event"/>
    <!-- Commercial Invoices Table -->
    <div v-if="dataRetrieved">
      <h3>Commercial Invoices</h3>
      <div v-for="(data, dataIndex) in commercialInvoices" :key="dataIndex">
        <h2>Invoice Data #{{ dataIndex + 1 }}</h2>
        <h3>Header Information</h3>
        <div class="form-row">
          <div class="row">
            <div class="col-md-2">
              <label for="InvoiceNumber" class="form-label"><strong>Invoice Number</strong></label>
              <input type="text" v-model="data.invoice_number" class="form-control" id="InvoiceNumber" placeholder="Enter Invoice Number">
            </div>
            <div class="col-md-2">
              <label for="SupplierName" class="form-label" ><strong>Supplier Name</strong></label>
              <input type="text" v-model="data.vendor_name" class="form-control" id="SupplierName" placeholder="Enter Supplier Name">
            </div>
            <div class="col-md-2">
              <label for="InvoiceDate" class="form-label"><strong>Invoice Date</strong></label>
              <input type="date" v-model="data.invoice_date" class="form-control" id="InvoiceDate">
            </div>
            <div class="col-md-2">
              <label for="invoiceCurrency" class="form-label"><strong>Invoice Currency</strong></label>
              <v-select :options="currencies" v-model="data.currency" label="name" id="invoiceCurrency" :searchable="true" :filterable="true" :reduce="currency => currency.code"></v-select>
            </div>
            <div class="col-md-2">
              <label for="rateOfExchange" class="form-label"><strong>Rate of Exchange</strong></label>
              <input type="number" class="form-control" id="rateOfExchange" v-model="data.rate_of_exchange" required>
            </div>
            <div class="col-md-2">
              <label class="form-label"><strong>Download Invoice</strong></label>
              <div>
                <a :href="data.file_url" target="_blank">
                <i class="fas fa-file-pdf fa-2x"></i>
              </a>
              </div>
            </div>
          </div>
        </div>
        <h3>Line Details</h3>
        <table>
          <thead>
            <tr>
              <th>Line #</th>
              <th>Product Code</th>
              <th>Description</th>
              <th>THN Code</th>
              <th>Duty</th>
              <th>VAT</th>
              <th>Unit Quantity</th>
              <th>Unit Price</th>
              <th>Extension Price</th>
              <!-- <th>Country of Origin</th>
              <th>CPC Code</th>
              <th>NPC Code</th> -->
              <th>Actions</th>
            </tr>
          </thead>
          <tbody>
            <template v-for="(item, itemIndex) in data.lines" :key="itemIndex">
              <tr >
                <td>{{ itemIndex + 1 }}</td>
                <td contenteditable="true" @blur="updateInvoiceData(item, 'product_code', $event.target.innerText)">{{ item.product_code || 'N/A' }}</td>
                <td contenteditable="true" @blur="updateInvoiceData(item, 'description', $event.target.innerText)">{{ item.description || 'N/A' }}</td>
                <td :class="[item.commodityCodeStatus === 'valid' ? 'valid-commodity-code': 'invalid-commodity-code']" contenteditable="true" @input="handleInput($event)" @blur="updateTariffCode(item, 'tariff_code', $event.target.innerText)">{{ item.tariff_code || 'N/A' }} </td>
                <td>{{ item.icdCharge }}%</td>
                <td>{{ item.vatCharge }}%</td>
                <td contenteditable="true" @blur="updateInvoiceData(item, 'quantity', $event.target.innerText)" :style="naStyle(item.quantity)">{{ item.quantity || 'N/A' }}</td>
                <td contenteditable="true" @blur="updateInvoiceData(item,'unit_price', $event.target.innerText)" :style="naStyle(item.unit_price)">{{ item.unit_price || 'N/A' }}</td>
                <td contenteditable="true" @blur="updateInvoiceData(item,'extension_price', $event.target.innerText)" :style="extensionStyle(item)">{{ item.extension_price || 'N/A' }}</td>
                <!-- <td>
                  <v-select :options="countries" v-model="item.country_of_origin" label="code" id="country" :searchable="true" :filterable="true" :reduce="country => country.code"></v-select>
                </td>
                <td>
                  <v-select :options="availableCPCCodes" v-model="item.cpcCode" label="code" id="cpcCode" :searchable="true" :filterable="true" :reduce="cpc => cpc.cpcId"></v-select>
                </td>
                <td>
                  <v-select v-model="item.npcCode" :options="npcCodeOptions" label="code" id="npcCode" :searchable="true" :reduce="npc => npc.code" :filterable="true" placeholder="Select a NPC Code"></v-select>
                </td> -->
              
                <td>
                  
                  <button @click="toggleDetails(itemIndex)">
                    <span v-if="expandedIndex === itemIndex">
                      <i class="fas fa-chevron-up" style="color:gray;"></i>
                    </span>
                    <span v-else>
                      <i class="fas fa-chevron-down" style="color:gray;"></i>
                    </span>
                  </button>
                </td>
              </tr>
              <tr v-if="expandedIndex === itemIndex">
                <td colspan="10">
                  <div class="row">
                    <div class="col-md-4">
                      <label for="countryOfOrigin" class="form-label"><strong>Country of Origin</strong></label>
                      <v-select :options="countries" v-model="item.country_of_origin" label="name" id="country" :searchable="true" :filterable="true" :reduce="country => country.code"></v-select>
                    </div>
                    <div class="col-md-4">
                      <label for="cpcCode" class="form-label"><strong>CPC Code</strong></label>
                      <v-select :options="availableCPCCodes" v-model="item.cpcCode" label="cpcDisplay" id="cpcCode" :searchable="true" :filterable="true" :reduce="cpc => cpc.cpcId"></v-select>
                    </div>
                    <div class="col-md-3">
                      <label for="npcCode" class="form-label"><strong>NPC Code</strong></label>
                      <v-select v-model="item.npcCode" :options="npcCodeOptions" label="code_description" id="npcCode" :searchable="true" :reduce="npc => npc.code" :filterable="true" placeholder="Select a NPC Code"></v-select>
                    </div>
                    <div class="col-md-1">
                      <label for="delete" class="form-label"><strong>Delete</strong></label>
                      <div><button class="btn btn-danger" @click="deleteRow(data, itemIndex)"><i class="fa fa-trash"></i></button></div>
                    </div>
                  </div>
                </td>
              </tr>

            </template>
          </tbody>
          <tfoot>
            <tr>
              <td colspan="7"><button @click="addNewLine(data)">Add New Line</button></td>
            </tr>
          </tfoot>
        </table>
        <h3>Footer Information</h3>
        <table>
          <thead>
            <tr>
              <th>Invoice Total</th>
              <th>Inland</th>
              <th>Insurance</th>
              <th>Other Charges</th>
              <th>Final Total</th>
            </tr>
          </thead>
          <tbody>
            <tr>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'invoice_total', $event.target.innerText)" :style="naStyle(data.invoice_total)">{{ data.invoice_total || 0.00 }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'inland', $event.target.innerText)" :style="naStyle(data.inland)">{{ data.inland || 0.00 }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'insurance', $event.target.innerText)" :style="naStyle(data.insurance)">{{ data.insurance || 0.00 }}</td>
              <td contenteditable="true" @blur="updateInvoiceData(data, 'other_charges', $event.target.innerText)" :style="naStyle(data.other_charges)">{{ data.other_charges || 0.00 }}</td>
              <td @blur="updateInvoiceData(data, 'final_total', $event.target.innerText)" >{{ calculateInvoiceTotal(data) }}</td>
            </tr>
          </tbody>
        </table>

        <h3>Calculated Check</h3>
        <table>
          <thead>
            <tr>
              <th>Total Extension Prices</th>
              <th>Number of Line Items</th>
              <th>Matches Invoice Total?</th>
            </tr>
          </thead>
          <tbody>
            <tr :style="checkInvoiceTotal(data)">
              <td>{{ calculateTotalExtensionPrice(data.lines) }}</td>
              <td>{{ data.lines.length }}</td>
              <td>{{ calculateTotalExtensionPrice(data.lines) === parseFloat(data.invoice_total).toFixed(2) ? 'Yes' : 'No' }}</td>
            </tr>
          </tbody>
        </table>
      </div>
      <div>
      <table>
        <thead>
          <tr>
            <th>Number of Lines Read</th>
            <th>Total Invoices Value</th>
          </tr>
        </thead>
       <tbody>
        <tr>
          <td>{{ this.totalNumberOfLines }}</td>
          <td>{{ this.totalFOBValue }}</td>
        </tr>
        </tbody>
      </table>
      <table>
        <tr>
          <td><button type="button" class="btn btn-success" :disabled="isGenerateCsvDisabled" @click="generateAsycudaXML">Generate Asycuda XML</button></td>
          <td><button type="button" class="btn btn-primary" @click="saveData">Save Entry</button></td>
          <td><button type="button" class="btn btn-primary" @click="goToCustomsDeclarationPage(this.entryId)">Generate Valuation Form</button></td>
          <td><button type="button" class="btn btn-primary" :disabled="isGenerateCsvDisabled" @click="generateWorkSheet" >Generate Worksheet</button></td>
          <td><button type="button" class="btn btn-primary" @click="generateDepositForm">Generate Deposit Form</button></td>
         
        </tr>
        <tr>
          <td><button type="button" class="btn btn-primary" @click="generateCARICOM">Generate CARICOM Invoice</button></td>
          <td><button type="button" class="btn btn-primary" :disabled="isGenerateCsvDisabled" @click="approveClassification">Approve Classification</button></td>
          <td><button type="button" class="btn btn-primary" @click="displayRatesTable">View Rates of Exchange</button></td>
          <td><button type="button" class="btn btn-primary" @click="generateTTC84">Generate Special Exemption</button></td>
          <td><button type="button" class="btn btn-primary" @click="generateOvertimeForm">Generate Overtime Form</button></td>
        </tr>
      </table>
    </div>
    </div>
    


  </div>
  
</template>

<script>
import axios from 'axios';
import * as SweetAlertConnector from '../composables/SweetAlertConnector';
import * as PackageTypes from '@/composables/PackageTypes';
import * as DatabaseConnector from '@/composables/DatabaseConnector';
import * as Utilities from '@/composables/Utilities';
import CustomsEntryForm from './CustomsEntry/CustomsEntryForm.vue';
import * as Currency from '@/composables/Currency'
import { CLASSIFICATION_STATUS } from '../constants'


import moment from 'moment-timezone';


export default {
  name: 'EntryDetails',
  components: {
    CustomsEntryForm
  },
  props: {
    entryId: {
      type: [String, Number],
      required: true
    }
  },
  watch: {
    totalFOBValue(newVal){
      this.customsEntryForm.totalCargoValue = newVal;
    }, 
    'customsEntryForm.waybillDate'(newDate) {
      this.updateRates(newDate);
    },
    commercialInvoices: {
      handler: function(invoices){
        console.log("Invoice Changes")
        console.log("Rates of Exchange: ", this.customsEntryForm.ratesOfExchange)
        if(this.customsEntryForm.ratesOfExchange.length > 0){
          console.log("Rate of Exchange: ", this.customsEntryForm.ratesOfExchange)
          invoices.forEach(invoice => {
            console.log("Invoice Currency: ", invoice.currency)
            this.$watch(() => invoice.currency, (newVal) => {
              if(newVal){
                console.log("New Currency: ", newVal)
                invoice.rate_of_exchange = this.customsEntryForm.ratesOfExchange.find(rate => rate.base_currency === newVal).rate
              }
            }, {deep: true, immediate: true});
          });
         
        }
      },
      deep: true,
      immediate: true
    }
  },
  computed: {
    totalFOBValue() {
      return this.commercialInvoices.reduce((acc, invoice) => acc + parseFloat(invoice.invoice_total), 0).toFixed(2);
    },
    toalInsuranceValue() {
      return this.commercialInvoices.reduce((acc, invoice) => acc + parseFloat(invoice.insurance), 0).toFixed(2);
    },
    totalInlandValue() {
      return this.commercialInvoices.reduce((acc, invoice) => acc + parseFloat(invoice.inland), 0).toFixed(2);
    },
    totalOtherChargesValue() {
      return this.commercialInvoices.reduce((acc, invoice) => acc + parseFloat(invoice.other_charges), 0).toFixed(2);
    },
    netInvoicePrice() {
      return parseFloat(this.totalFOBValue) + parseFloat(this.toalInsuranceValue) + parseFloat(this.totalInlandValue) + parseFloat(this.totalOtherChargesValue);
    },
    formattedDate: {
      get() {
        const date = new Date(this.entryDetails.waybill_date);
        return date.toISOString().split('T')[0];
      },
      set(newValue) {
        this.entryDetails.waybill_date = newValue;
      }
    },
    isGenerateCsvDisabled() {
      return this.commercialInvoices.some(invoice => {
        return invoice.lines.some(line => !line.tariff_code || line.tariff_code.trim() === '');
      });
    },
    classificationStatus(){
      const classificationResult =  Utilities.getClassificationStatus(this.commercialInvoices) ? CLASSIFICATION_STATUS.NOT_CLASSIFIED : CLASSIFICATION_STATUS.COMPLETED;
      if(classificationResult === false)
        return CLASSIFICATION_STATUS.NOT_CLASSIFIED;
      else{
        if(this.classificationApproved === 1) return CLASSIFICATION_STATUS.APPROVED;
         else return CLASSIFICATION_STATUS.NOT_CLASSIFIED;
      }
    },
    availableCPCCodes(){
      if(this.customsEntryForm.regimeTypeCode !== undefined && this.customsEntryForm.regimeTypeCode !== null){
        const regimeData = this.customsEntryForm.regimeTypeOptions.find(regime => regime.regimeTypeCode  === this.customsEntryForm.regimeTypeCode);
        if(regimeData){
          return regimeData.cpcCodes
        } else return []
      }
      else{
        return this.customsEntryForm.regimeTypeOptions.flatMap(regime => regime.cpcCodes)
      }
    }
  },
  data() {
    return {
      entryDetails: null,
      waybill: {},
      entry: {},
      commercialInvoices: [],
      linesToBeDeleted: [],
      dataRetrieved: false,
      loading: false,
      error: null,
      countries: [],
      totalNumberOfLines: 0,
      modeOfTransportOOptions: ['Air', 'Ocean'],
      freightTypeOptions: ['FCL', 'LCL', 'Bulk', 'Break Bulk'],
      cpcCodeOptions: ['4000'],
      npcCodeOptions: ['000'],
      packageTypeOptions: [
      ],
      currencies: [],
      classificationApprovedBy: '',
      expandedIndex: null,
      classificationApproved: 0,
      customsEntryForm: {
        exporters: [],
            importers: [],
            vessels: [],
            ports: [],
            declarants: [],
            incotermsList: [],
            ratesOfExchange: [],
            importerId: '',
            exporterId: '',
            importerName: '',
            exporterName: '',
            importerTaxIdentificationNumber: '',
            exporterTaxIdentificationNumber: '',
            exporterCountry: '',
            declarantId: '',
            declarantTaxIdentificationNumber: '',
            modeOfTransport: '',
            freightType: '',
            waybillNumber: '',
            waybillDate: '',
            vesselId: '',
            vesselName: '',
            incoTerms: '',
            portOfDischargeId: '',
            portOfDischargeName: '',
            netWeight: 0.0,
            grossWeight: 0.0,
            numberOfPackages: 0,
            packageType: '',
            packageTypes: [],
            marksAndNumbers: 'As Addressed',
            freightCurrency: 'USD',
            insuranceCurrency: 'USD',
            otherChargesCurrency: 'USD',
            totalCargoValue: 0.0,
            freightAmount: 0.0,
            insuranceAmount: 0.0,
            otherCharges: 0.0,
            insuranceRateOfExchange: 0.0,
            otherChargesRateOfExchange: 0.0,
            freightRateOfExchange: 0.0,
            rateOfExchange: 0.0,
            regimeType: '',
            regimeTypeCode: '',
            regimeTypeOptions: [],
            depositAmount: 0.0,
            containerCharges: 0.0,
            additionalCharges: 0.0,
            localFee: 0.0,
            countryOfOrigin: '',
            countryOfFinalDestination: 'TT',
            countryLastProvinance: '',
            tradingCountry: ''
      }
    };
  },
  mounted() {
    this.fetchAllDetails();
    this.fetchCountries();
    this.fetchNpcCodes();
    this.packageTypeOptions = PackageTypes.usePackageTypes();
    this.currencies = Currency.getCurrencyList();
    

  },
  methods: {
    calculateInvoiceTotal(invoice){
      const invoiceTotal = parseFloat(invoice.invoice_total) || 0;
      const inland = parseFloat(invoice.inland) || 0;
      const insurance = parseFloat(invoice.insurance) || 0;
      const otherCharges = parseFloat(invoice.other_charges) || 0;

      const total = invoiceTotal + inland + insurance + otherCharges;

      return total.toFixed(2);
    },
    displayRatesTable(){
      if(this.customsEntryForm.waybillDate === undefined || this.customsEntryForm.waybillDate === null || this.customsEntryForm.waybillDate.trim().length === 0){
        SweetAlertConnector.showErrorAlert('Please Enter a valid Waybill Date to get rates of Exchange!');
        return;
      }
      if(this.customsEntryForm.ratesOfExchange.length === 0)
        this.updateRates(this.customsEntryForm.waybillDate);
      const currencies = [this.customsEntryForm.freightCurrency, this.customsEntryForm.insuranceCurrency, this.customsEntryForm.otherChargesCurrency]
      const ratesToDisplay = this.customsEntryForm.ratesOfExchange.filter(rate => currencies.includes(rate.base_currency));
      SweetAlertConnector.displayRateOfExchangeTable(ratesToDisplay);
    },
    updateRates(shippedOnBoardDate){
      console.log("Updating Rates");
      DatabaseConnector.fetchRatesBasedOnShippedOnBoard(shippedOnBoardDate).then((rates) => {
        console.log('Rates:', rates);
        this.customsEntryForm.ratesOfExchange = rates;
        this.commercialInvoices.forEach(invoice => {
          if(invoice.currency && rates.length > 0){
            console.log("Invoice Currency: ", invoice.currency)
            invoice.rate_of_exchange = rates.find(rate => rate.base_currency === invoice.currency).rate
          }
        });
      });
    },
    verifyClassificationCodes() {
        let hasInvalidCode = false;

        this.commercialInvoices.forEach(invoice => {
            invoice.lines.forEach(line => {
                console.log("Line: ", line.commodityCodeStatus);
                if (line.commodityCodeStatus === "invalid") {
                    SweetAlertConnector.showErrorAlert(`The following commodity code does not exist on Asycuda - ${line.tariff_code}. Please update this and try again.`);
                    hasInvalidCode = true;
                }
            });
        });

        return !hasInvalidCode;
    },
    approveClassification(){
      if(!this.verifyClassificationCodes()){
        return;
      }
      console.log("Approving Classification")
      SweetAlertConnector.enterYourName().then((response) => {
        const name = response;
        console.log("Name: ", name)
        this.classificationApprovedBy = name;
        this.classificationApproved = 1;
        this.saveData();
      })
     
    },
    toggleDetails(index) {
        this.expandedIndex = this.expandedIndex === index ? null : index;
    },
    inializeTaxes(){
      this.commercialInvoices.forEach(invoice => {
        invoice.lines.forEach(line => {
          if(line.tariff_code !== undefined && line.tariff_code !== null && line.tariff_code.trim().length > 0 && line !== 'N/A') {
            this.updateTariffCode(line, 'tariff_code', line.tariff_code);
          }
        });
      });
    },

    updateTariffCode(data, field, value) {
      this.updateInvoiceData(data, field, value);
      if(value === undefined || value === null || value.trim().length === 0 || value === 'N/A'){
        data.icdCharge = 0;
        data.vatCharge = 0;
        return;
      }
      DatabaseConnector.getCommodityCharges(value.replace(/\./g, '')).then(chargesResponse => {
        let icdObject = chargesResponse.charges['import custom duty'];
        const icdCharge = icdObject === undefined ? 0 : parseFloat(icdObject[0].charge_amount);
        let vatObject = chargesResponse.charges['value added tax'];
        const vatCharge = vatObject === undefined ? 0 : parseFloat(vatObject[0].charge_amount);
        data.icdCharge = icdCharge
        data.vatCharge = vatCharge
        data.commodityCodeStatus = 'valid';
      }).catch(error => {
        console.error('Failed to get commodity charges:', error);
        data.commodityCodeStatus = 'invalid';
        data.icdCharge = 0;
        data.vatCharge = 0;
      });
    },
    setInvoiceLineWorksheetData(){
      let promises = [];
      this.commercialInvoices.forEach(invoice => {
        invoice.lines.forEach(item => {
          item.cpcCodeName = this.availableCPCCodes.find(cpc => cpc.cpcId === item.cpcCode)?.code
          const promise = DatabaseConnector.getCommodityCharges(item.tariff_code.replace(/\./g, '')).then(chargesResponse => {
            let formattedCharges = Utilities.formatCharges(chargesResponse.charges, item.vatApplicable)
            item.commodity = {
              code: item.tariff_code.replace(/\./g, ''),
              id: chargesResponse.id,
              charges: formattedCharges
            }
            // console.log("Commodity Charges: ", item.commodity)
          }).catch(error => {
            SweetAlertConnector.closeCurrentAlert();
            SweetAlertConnector.showErrorAlert(`The following commodity code does not exist on Asycuda - ${item.tariff_code}. Please update this and try again. `);
            console.error('Failed to get commodity charges:', error);
            return Promise.reject(error);
            
          });
          promises.push(promise)
        })
      })

      return Promise.all(promises)
    },
    generateCARICOM(){
      console.log("Generating CARICOM")
      const countryOfOrigin = this.countries.find(country => country.code === this.customsEntryForm.countryOfOrigin)?.name;
      const countryOfFinalDestination = this.countries.find(country => country.code === this.customsEntryForm.countryOfFinalDestination)?.name;
      const selectedIncoTerms = this.customsEntryForm.incotermsList.find(incoTerm => incoTerm.code === this.customsEntryForm.incoTerms)?.name;
      const selectedCurrency = this.currencies.find(currency => currency.code === this.commercialInvoices[0].currency)?.name;
      const selectedImporter = this.customsEntryForm.importers.find(importer => importer.id === this.customsEntryForm.importerId);
      const importerAddress = selectedImporter === undefined ? '' : selectedImporter.address;
      const importerCountryName = this.countries.find(country => country.code === selectedImporter.country)?.name;
      const importNameAddress = `${this.customsEntryForm.importerName}\n${importerAddress}\n${importerCountryName}`.toUpperCase();
      const selectedExporter = this.customsEntryForm.exporters.find(exporter => exporter.id === this.customsEntryForm.exporterId);
      const exporterAddress = selectedExporter === undefined ? '' : selectedExporter.address;
      const exporterCountryName = this.countries.find(country => country.code === selectedExporter.country)?.name;
      const exporterNameAddress = `${this.customsEntryForm.exporterName}\n${exporterAddress}\n${exporterCountryName}`.toUpperCase();
      
      

      SweetAlertConnector.caricomAlert().then((response) => {
        const caricomObject = Utilities.formatCaricomData(importNameAddress, exporterNameAddress, this.entryDetails.entry_number, countryOfOrigin, selectedIncoTerms, this.customsEntryForm.portOfDischargeName, countryOfFinalDestination, this.customsEntryForm.modeOfTransport,  this.customsEntryForm.vesselName, selectedCurrency, response, this.customsEntryForm.grossWeight, this.commercialInvoices, this.customsEntryForm.numberOfPackages, this.customsEntryForm.totalCargoValue, this.customsEntryForm.freightAmount, this.customsEntryForm.insuranceAmount, this.customsEntryForm.otherCharges);
        SweetAlertConnector.showLoadingAlert('Generating CARICOM Invoice...');

        DatabaseConnector.generateCaricomInvoice(caricomObject).then((response) => {
          SweetAlertConnector.closeCurrentAlert();
          SweetAlertConnector.showSuccessAlert('CARICOM Invoice Generated Successfully!');
          const fileURL = window.URL.createObjectURL(new Blob([response], { type: 'application/pdf' }));
          const fileLink = document.createElement('a');
