export function getConfigurations(){
    const configuration = {
        defaultCPCCode: "4000",
        defaultRegimeType: "IM4",
        defaultNPCCode: "000"
    }
    return configuration;
}

export function getFreightTypes(){
    const freightTypes = [
        {
            modeOfTransport: "Ocean",
            freightTypes: [
                { code: "12", name: "FCL" },
                { code: "13", name: "LCL" },
                { code: "11", name: "Break Bulk" },
            ]
        },
        {
            modeOfTransport: "Air",
            freightTypes: [
                { code: "41", name: "Express" },
                { code: "42", name: "Loose" },
            ]
        }
    ]
    return freightTypes;
}